#footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    padding: 10px 20px;
    box-shadow: rgba(0, 0, 0, 0.8) 0 1px 1px 0px;
    z-index: 10;
    display: flex;
    justify-content: end;
}
@media only screen and (max-width: 768px) {
    #footer {
        background: white;
    }
}